import React, { PropsWithChildren } from 'react';
import { View, Text, Image } from 'react-native';
import { Project } from '../../../App';
import { CustomPressable } from '../../components/buttons/CustomPressable';
import PhoneFrame from '../../components/PhoneFrame';
import useTheme from '../../hooks/useTheme';
import { usePopup } from './PopupProvider';

interface Props {
  title: string;
  text?: string;
  closeOnCTA?: boolean;
  project: Project;
}

const PopupContent = ({ title, text, closeOnCTA, children, project }: PropsWithChildren<Props>) => {
  const theme = useTheme();
  const { closePopup } = usePopup();

  return (
    <View
      style={{
        marginVertical: theme.space.l,
        marginHorizontal: theme.space.l,
      }}
    >
      <>
        <View style={{ flexDirection: 'row' }}>
          {project?.appScreenshotSources && (
            <PhoneFrame>
              {project?.appScreenshotSources && (
                <Image
                  source={project.appScreenshotSources[0]}
                  style={{
                    flex: 1,
                  }}
                  resizeMode="cover"
                />
              )}
            </PhoneFrame>
          )}
          <View style={{ flex: 1, paddingLeft: project?.appScreenshotSources ? theme.space.m : 0 }}>
            <Text
              style={{ textAlign: 'center', marginBottom: theme.space.m, fontSize: 24, fontWeight: 'bold' }}
            >
              {title}
            </Text>
            {!!text && <Text style={{ textAlign: 'left', lineHeight: 26 }}>{text}</Text>}
          </View>
        </View>
        {children}

        <View
          style={{
            width: '100%',
            marginTop: theme.space.l,
          }}
        >
          <CustomPressable
            onPress={() => {
              if (closeOnCTA) closePopup();
            }}
            style={{
              alignSelf: 'flex-end',
            }}
          >
            <View
              style={{
                backgroundColor: 'tomato',
                paddingVertical: theme.space.m,
                paddingHorizontal: theme.space.xl,
                alignItems: 'center',
                borderRadius: 4,
              }}
            >
              <Text style={{ color: 'white' }}>Close</Text>
            </View>
          </CustomPressable>
        </View>
      </>
    </View>
  );
};
export default PopupContent;
