import { Project } from '../../App';
import React from 'react';
import { Text, View, useWindowDimensions } from 'react-native';
import { CustomPressable } from './buttons/CustomPressable';
import { usePopup } from '../navigation/popup/PopupProvider';
import PopupContent from '../navigation/popup/PopupContent';
import AppIcon from './AppIcon';

export default function ProjectCard({ theme, p }: { theme: any; p: Project }) {
  const { width } = useWindowDimensions();
  const { openPopup } = usePopup();
  return (
    <CustomPressable
      onPress={() => {
        openPopup({
          component: <PopupContent title={p.title} text={p.text} closeOnCTA project={p} />,
          disableBackdropClose: true,
        });
      }}
      style={{
        flex: width < 800 ? undefined : width < 1200 ? '33%' : '25%',
      }}
    >
      <View
        style={{
          backgroundColor: 'white',
          padding: theme.space.l,
          //minWidth: width < 800 ? undefined : 300,
          //maxWidth: width < 800 ? undefined : '300',
          borderRadius: 4,
          minHeight: '100%',
        }}
      >
        <View style={{ flexDirection: 'row', alignItems: 'center' }}>
          {p.logoComp && (
            <View style={{ marginBottom: theme.space.s, marginRight: theme.space.l }}>{p.logoComp}</View>
          )}
          {p.appIconSources && (
            <AppIcon appIconSource={p.appIconSources[0]} style={{ marginRight: theme.space.l }} />
          )}
          <View style={{ flex: 1, gap: theme.space.m }}>
            <Text
              style={{
                fontSize: 20,
                fontWeight: 'bold',
              }}
            >
              {p.title}
            </Text>
            <Text
              style={{
                fontStyle: 'italic',
              }}
            >
              {p.subtitle}
            </Text>
          </View>
        </View>
        {p.text && (
          <View style={{ marginTop: theme.space.m }}>
            <Text>{p.text.length > 200 ? p.text.slice(0, 200) + '...' : p.text}</Text>
          </View>
        )}
      </View>
    </CustomPressable>
  );
}
